import constant from '@/common/constant'
import http from '@/utils/http'

export function pageAllAlarms(params) {
  return http.get(`${constant.baseUrl}/v2/alarms/${params.notice_status}`, params)
}

export function findPlaceDetail(id) {
  return http.get(`${constant.baseUrl}/v3/place/${id}`)
}

export function exportAlarmsExcel(start, end, pid, fileName, alarmtype, status) {
  let url = `${constant.baseUrl}/v2/alarms/excel?start=${start}&end=${end}`
  if (pid >= 0) {
    url += `&place_id=${pid}`
  }
  if (alarmtype >= 0) {
    url += `&alarm_type=${alarmtype}`
  }
  if (status >= 0) {
    url += `&status=${status}`
  }
  return http.download(url, fileName)
}

export function queryTask(params) {
  return http.get(`${constant.baseUrl}/v2/tasks`, params)
}
