 -->
<template>
  <div class="malfunction">
    <title-header title="派工单管理"></title-header>
    <div class="selects">
      <div class="select-titles">
        <el-row :gutter="24">
          <el-col :span="8"><div class="common-item">工单状态</div></el-col>
          <el-col :span="8"><div class="common-item"></div></el-col>
        </el-row>
      </div>
      <div class="select-content">
        <el-row :gutter="24">
          <el-col :span="8">
            <div class="common-item selsct-common">
              <el-select v-model="queryFilter.status" placeholder="请选择">
                <el-option
                  v-for="item in orderStatus"
                  :key="item.name"
                  :label="item.name"
                  :value="item.code">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="8">
            <el-button class="blue" type="primary" @click="initData">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-alarm_type" slot-scope="scope"><p>{{alarmType(scope.row.alarm_type)}}</p></div>
        <div slot="col-alarm_time" slot-scope="scope"><p>{{calcTime(scope.row.alarm_time)}}</p></div>
        <div slot="col-create_time" slot-scope="scope"><p>{{calcTime(scope.row.create_time)}}</p></div>
        <div slot="col-end_time" slot-scope="scope"><p>{{calcTime(scope.row.end_time)}}</p></div>
        <div slot="col-status" slot-scope="scope"><p>{{pgStatus(scope.row.status)}}</p></div>
        <div slot="col-confirm" slot-scope="scope">
            <el-button @click="handleShowOrder(scope.row)" type="text" size="small">处理工单</el-button>
            <el-button @click="handleShowStep(scope.row)" type="text" size="small">查看进度</el-button>
        </div>
      </iot-table>
    </div>
    <handle-order ref="handleOrder" v-on:handle-success="initData"></handle-order>
    <handle-step ref="handleStep"></handle-step>
  </div>
</template>

<script>
import constant from '@/common/constant'
import { queryTask } from '@/api/hiddendanger'
import dateUtil from '@/utils/dateUtil'
import handleOrder from '@/views/monitor/components/dialogs/handleOrder'
import handleStep from '@/views/monitor/components/dialogs/alarmHandleSteps'
import iotTable from '@/components/iotTable'
import TitleHeader from '@/components/titleHeader'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Malfunction',
  components: {
    handleOrder,
    handleStep,
    iotTable,
    TitleHeader
  },
  mounted() {
    this.initData()
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        status: '1,2,3,4',
        delayAmount: 0
      },
      orderStatus: [
        {
          code: '1,2,3,4',
          name: '全部事件'
        },
        {
          code: '1',
          name: '派单中'
        },
        {
          code: '2',
          name: '处理中'
        },
        {
          code: '3',
          name: '延迟处理'
        },
        {
          code: '4',
          name: '处理完成'
        }
      ],
      project: {
        name: '',
        id: ''
      },
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'alarm_type', label: '报警类型', type: 'slot' },
        { prop: 'address', label: '安装位置' },
        { prop: 'alarm_time', label: '报警时间', type: 'slot' },
        { prop: 'create_time', label: '创建时间', type: 'slot' },
        { prop: 'end_time', label: '完成时间', type: 'slot' },
        { prop: 'mac', label: '设备mac' },
        { prop: 'status', label: '派工状态', type: 'slot' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  computed: {
    // 报警类型
    alarmType() {
      return (alarm_type) => {
        return constant.alarmTypes[alarm_type]
      }
    },
    // 派工状态
    pgStatus() {
      return (status) => {
        if (status === 0 || status === 3) {
          return `${status === 0 ? '未派工' : '延迟处理'}`
        } else if (status === 4) {
          return '处理完毕'
        } else {
          return `${status === 1 ? '派单中' : '处理中'}`
        }
      }
    },
    // 时间
    calcTime() {
      return (time) => {
        return time ? dateUtil.format(new Date(time * 1000), 'YYYY-MM-dd HH:mm:ss') : ''
      }
    }
  },
  methods: {
    handleShowOrder(row) {
      this.$refs.handleOrder.show(row)
    },
    handleShowStep(row) {
      this.$refs.handleStep.show(row)
    },
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        status: this.queryFilter.status,
        delay_amount: this.queryFilter.delayAmount ? this.queryFilter.delayAmount : undefined,
        place_id: this.project.id ? this.project.id : undefined
      }

      try {
        const { data } = await queryTask(params)
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('查询数据失败')
      }
    }
  }
}
</script>

