<!--
 * @Description: 封装的table组件
 * @Author: benlong
 * @Date: 2019-09-12 21:37:46
 -->
<template>
  <div class="iotTable">
    <el-table :data="tableData" style="width: 100%" v-loading="loading">
      <el-table-column v-for="(item, index) in tableHeader" :key="index" v-if="item.type !== 'slot'"  :prop="item.prop" :width="item.width" :label="item.label"/>
      <el-table-column :width="item.width || 150" :label="item.label" :key="index" v-else>
        <template slot-scope="scope">
          <slot :name="'col-' + item.prop" :row="scope.row" :index="scope.$index"></slot>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container" v-if="showPage">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-sizes="[10,20,50,100]"
        :page-size="pagination.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IotTable',
  props: {
    tableData: {
      type: Array,
      required: true
    },
    tableHeader: {
      type: Array,
      required: true
    },
    loading: {
      type: Boolean,
      required: false
      // default: true
    },
    pagination: {
      type: Object,
      required: false
    },
    hidepagination: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data() {
    return {

    }
  },
  computed: {
    showPage() {
      return !this.hidepagination && !!this.pagination
    }
  },
  methods: {
    // 当前直接修改父组件的pagenation数据, 还可以通过emeit让父组件自己去修改自己的paginationshuju,但是父组件都需要多些两个方法
    // handleSizeChange(val) {
    //   this.$emit('pageSizeChange', val)
    // },
    // handleCurrentChange(val) {
    //   this.$emit('currentPageChange', val)
    // }
    handleSizeChange(val) {
      this.pagination.per_page = val
      this.$emit('reloaddata')
    },
    handleCurrentChange(val) {
      console.log(val)
      this.pagination.page = val
      this.$emit('reloaddata')
    }
  }
}
</script>

<style lang="less" scoped>
  .iotTable{
    // width: 100%;
  }
</style>
